.image_gallery_modal.true {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: black;
  background: rgba(0, 0, 0, 0.8);
  z-index: 99;
}

.modal > div {
  position: absolute;
}

.modal-image-carousel {
  position: absolute;
  bottom: 0;
  width: 50vw;
}

.modal-background.true {
  background: rgba(0, 0, 0, 0.7);
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  bottom: 0;
  z-index: 1;
}

.modal-close-btn a {
  position: absolute;
  right: -22px;
  top: 4px;
  color: #fff;
  cursor: pointer;
  z-index: 1;
}

.modal-thumbnails.true {
  position: fixed;
  display: flex;
  bottom: 15px;
  left: 0;
  right: 0;
  z-index: 1;
  justify-content: center;
  line-height: 150px;
}

.modal-thumbnails img {
  max-height: 152px;
  max-width: 100%;
  height: auto;
  height: initial;
  width: auto;
  width: initial;
  vertical-align: middle;
}

.image_gallery_slide {
  float: left;
  display: block;
  position: relative;
  width: 100%;
  height: 100%;
}

.image_gallery_slide img {
  display: none;
  position: absolute;
  top: 50%;
  left: 50%;
  max-width: 100%;
  max-height: 100%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  cursor: zoom-in;
}

.image_gallery_slide:first-child {
  left: 0;
}

.image_gallery_slide img.preview,
.image_gallery_slide img.loaded {
  display: inline-block;
}

.loaded {
  width: auto;
  width: initial;
  height: auto;
  height: initial;
}

.image_gallery_thumbs.true {
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 6;
  width: 100%;
  height: 168px;
  overflow: auto;
  overflow-y: hidden;
  -webkit-overflow-scrolling: touch;
  white-space: nowrap;
  padding: 0 28px 14px;
  text-align: center;
  box-sizing: border-box;
}

.thumb {
  margin: 0 3px;
  cursor: pointer;
  border: solid 1px transparent;
  width: 142px;
  height: 152px;
  background: rgba(0,0,0,0.5);
  text-align: center;
}

.thumb.current-thumb {
  border: solid 1px #fff;
}

.fas.fa-chevron-right,
.fas.fa-chevron-left {
  color: #fff;
  font-size: 65px;
  color: rgba(255, 255, 255, 0.6);
}

.nav-btns {
  position: absolute;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100vh;
  top: 0;
  bottom: 0;
  z-index: 5;
}

.arrow-left,
.arrow-right {
  padding: 0 25px;
  cursor: pointer;
}

.fas.fa-chevron-right:hover,
.fas.fa-chevron-left:hover {
  color: rgba(255, 255, 255, 1);
}

.label-thumbnail, 
.release-thumbnail {
  width: 50px;
  height: 50px;
  float: left;
  padding: 0 10px;
}

.hide {display:none;}

.release-title {
  display: inline-flex;
  align-items: center;
  height: 46px;
  width: 90%;
  justify-content: space-between;
}

.bottom-discography {
  display: flex;
}

.discography-left ul {padding:0; list-style: none;}
.discography-left ul li.current { padding: 2px 4px; background: #72A3D8; border-radius: 3px;}
.discography-left ul li a {font-size: 14px;font-weight: 400;line-height: 1.8; color: #0033BB;}
.discography-left ul li a:hover {text-decoration: underline;}
.discography-search { height:27px; border-radius: 4px; border: solid 1px #ccc; outline: none; text-indent: 4px; width: 100%;}

.discography-left {
  width: 205px;
  padding: 0 15px 0 0px;
}

.current a h4 {color:#fff!important;}

.search-box { position: relative; }

.fas.fa-search {
  position: absolute;
  right: 8px;
  top: 8px;
}

ul a h4 {margin:0;color:#0033BB;}

.logo.logo-search.artist {
  height: 100px;
}


